<template>
  <div :class="name">
      <div class="swiper-wrapper">
        <slot></slot>
      </div>
      <!-- 如果需要分页器 -->
      <div v-if="isPagination" class="swiper-pagination"></div>

      <!-- 如果需要导航按钮 -->
      <div v-if="isPrev" class="swiper-button-prev"></div>
      <div v-if="isNext" class="swiper-button-next"></div>

      <!-- 如果需要滚动条 -->
      <div v-if="isScrollbar" class="swiper-scrollbar"></div>
    </div>
</template>

<script>
export default {
    props:{
      name:{
        type:String,
        default:'swiper-container'
      },
      isPagination:{
        type:Boolean,
        default:false
      },
      isPrev:{
        type:Boolean,
        default:false
      },
      isNext:{
        type:Boolean,
        default:false
      },
      isScrollbar:{
        type:Boolean,
        default:false
      },
    },
}

</script>
<style lang="scss">
.swiper-container {
  width: 100%;
  height: calc(56vw);
}
</style>